""


import { useEffect, useState } from "react";

function FormModalPopUp() {
    const [open, setOpen] = useState(false);

    // Auto open modal after 10 sec
    useEffect(() => {
        const timer = setTimeout(() => setOpen(true), 10000);
        return () => clearTimeout(timer);
    }, []);

    const [formData, setFormData] = useState({
        phone: "9540663729",
        email: "jatin@realtimebiometrics.com",
        activity: "CLICK",
        activity_url:
            "https://realtimebiometrics.com/12312312312312312312312312312312312313131231313131313",
        country: "India",
        zipcode: "110092",
        state: "Delhi",
        area: "Delhi"
    });

    // Handle Input Change
    const handleChange = (e: any) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    };

    // Submit POST Request
    const handleSubmit = async (e: any) => {
        e.preventDefault();

        try {
            const res = await fetch(
                "https://markvisitor.com/app/dev/website/saveactivity.php",
                {
                    method: "POST",
                    headers: { "Content-Type": "application/json" },
                    body: JSON.stringify(formData),
                }
            );

            const data = await res.json();
            console.log("Submitted:", data);
            alert("Submitted Successfully!");
            setOpen(false);
        } catch (error) {
            console.error(error);
            alert("Error submitting form!");
        }
    };

    if (!open) return null;

    return (
        <div className="fixed inset-0 bg-black/60 flex items-center justify-center z-50 p-4">
            <div className="bg-white rounded-xl w-full max-w-lg p-6 shadow-lg relative">
                <h2 className="text-xl font-semibold mb-4">Activity Tracking Form</h2>

                {/* Close Button */}
                <button
                    className="absolute top-2 right-2 text-gray-600 hover:text-black"
                    onClick={() => setOpen(false)}
                >
                    âœ•
                </button>

                <form onSubmit={handleSubmit} className="space-y-4">

                    {/** Dynamic form fields */}
                    {Object.keys(formData).map((key) => (
                        <div key={key} className="flex flex-col">
                            <label className="font-medium capitalize">{key}</label>
                            <input
                                type="text"
                                name={key}
                                value={(formData as any)[key]}
                                onChange={handleChange}
                                className="border p-2 rounded-md"
                            />
                        </div>
                    ))}

                    <button
                        type="submit"
                        className="w-full bg-blue-600 text-white py-2 rounded-md mt-4 hover:bg-blue-700 transition"
                    >
                        Submit Activity
                    </button>
                </form>
            </div>
        </div>
    );
}

export default FormModalPopUp;